const React           = require("react");
const _               = require("lodash/core");

const ColumnsStore   = require("../stores/columns_store");

//Mixins
const cssMixins  = require("morse-react-mixins").css_mixins;
const textMixins = require("morse-react-mixins").text_mixins;

const Calendar       = require("./calendar")
    , PrintBtn       = require("./print_btn");

class TopBar extends React.Component {
  constructor(props) {
    super(props);
  }



  render() {
    return (
      <div className="clearfix calendar-bar">
        <Calendar device={this.state.device} />
        <PrintBtn print={this.props.print} />
      </div>
    );
  }
}

Object.assign(TopBar.prototype, cssMixins);
Object.assign(TopBar.prototype, textMixins);

module.exports = TopBar;